.App {
  font-family: 'Roboto', sans-serif;
}

/* .header-image {
  width: 100%;
  height: auto;
  position: relative;
  min-height: 200px;
} */

.hero-image {
    background: -webkit-gradient(
                linear, 
                left top, left bottom, 
                from(rgba(20,20,20, .3)), 
                to(rgba(20,20,20, .3))),
                url("/assets/home-page.jpg");
    background: -webkit-linear-gradient(
                rgba(20,20,20, .3), 
                rgba(20,20,20, .3)),
                url("/assets/home-page.jpg");
    background: -o-linear-gradient(
                rgba(20,20,20, .3), 
                rgba(20,20,20, .3)),
                url("/assets/home-page.jpg");
    background: linear-gradient(
                rgba(20,20,20, .3), 
                rgba(20,20,20, .3)),
                url("/assets/home-page.jpg");    
  /* background-image: url("/assets/home-page.jpg"); */
  height: 550px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-image-blog {
  background: -webkit-gradient(
    linear, 
    left top, left bottom, 
    from(rgba(20,20,20, .3)), 
    to(rgba(20,20,20, .3))),
    url("/assets/blog-header.jpg");
  background: -webkit-linear-gradient(
    rgba(20,20,20, .3), 
    rgba(20,20,20, .3)),
    url("/assets/blog-header.jpg");
  background: -o-linear-gradient(
    rgba(20,20,20, .3), 
    rgba(20,20,20, .3)),
    url("/assets/blog-header.jpg");
  background: linear-gradient(
    rgba(20,20,20, .3), 
    rgba(20,20,20, .3)),
    url("/assets/blog-header.jpg");    
  height: 550px;
  /* text-shadow: 2px 2px 7px #1A1919; */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  text-shadow: 2px 2px 7px #4E4C4C;
}

.card {
  margin-top: 50px;
}

.blogPost {
  /* text-align: center; */
  font-size: 16px;
  margin-top: 70px;
  max-width: 740px;
}

.industryHeader {
  text-align: center;
  margin-bottom: 45px;
  margin-top: 45px;
  text-shadow: 0.5px 0.5px 0.5px #F3F0F0;
  color: rgba(0,0,0,.8);
  font-size: 42px;
}

.professionHeader {
  text-align: center;
  margin-bottom: 45px;
  margin-top: 25px;
  text-shadow: 0.5px 0.5px 0.5px #F3F0F0;
  color: rgba(0,0,0,.8);
  font-size: 42px;
}

.cardtext {
  font-size: 32px;
}

.card-image {
  margin-bottom: 25px;
}

.blogContent {
  font-size: 21px;
  line-height: 1.58;
  letter-spacing: -.003em;
}

.avatar {
  margin-left: 250px;
  display: inline-block;
}

.author-bio {
  font-size: 16px;
}

.paper-text {
  position: absolute;
  font-size: 28px;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #F93434;
  text-shadow: 0.5px 0.5px 0.5px #F3F0F0;
}

.education-quote {
  text-align: center;
  color: #000000;
  font-size: 28px;
  left: 50%;
  text-shadow: 0.5px 0.5px 0.5px #F3F0F0;
  -webkit-box-shadow: 2px 2px 7px #000000;
          box-shadow: 2px 2px 7px #000000;
  margin-top: 40px;
  margin-bottom: 40px;
}

.home-content {
  font-size: 18px;
  margin-bottom: 30px;
}
 .paragraph {
   margin-top: 29px;
 }

 .seth-godin-quote {
   text-align: center;
   font-size: 24px;
   margin-top: 25px;
   font-weight: bold;
 }

.footer{
  height: 200px;
  margin-top: 60px;
  text-align: center;
  color: #fff;
  padding-top: 60px;
  font-size: 24px;
  background: rgb(27, 127, 145);
}





